.ProjectIcon {
  border-color: black;
  border-style: solid;
  border-width: 0px;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
}

.ProjectIcon.Container:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
}

.ProjectIcon.Container:hover .ProjectIcon#bulb p {
  color: #15bf50;
}

.ProjectIcon.Container:hover .ProjectIcon#bulb {
    background-color: #fffca8;
}

.ProjectIcon.Container:hover .ProjectIcon.bulbBottom {
    background-color: #dddddd;
}

.ProjectIcon.Container {
  width: 48px;
  height: 64px;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-weight: bold;
}

.ProjectIcon#bulb {
  width: 40px;
  height: 40px;
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  border-width: 1.5px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.ProjectIcon.bulbBottom {
  z-index: -1;
  -webkit-transform: translateY(-2px);
      -ms-transform: translateY(-2px);
          transform: translateY(-2px);
  width: 20px;
  height: 3px;
  border-bottom-width: 1.5px;
  border-left-width: 1.5px;
  border-right-width: 1.5px;
}

.ProjectIcon.bulbBottom#last {
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
}



