.Feed {
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
}

.Feed.ContentWindow {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  min-height: 50vw;
}

.Feed.PostFlow {
  width: 100%;
  display: grid;
  grid-template-columns: 50% 50%;
}

.Feed.PostFlow .Right {
  -webkit-transform: translateX(-3.33px);
      -ms-transform: translateX(-3.33px);
          transform: translateX(-3.33px);
}

.Feed.PostFlow .left {
  -webkit-transform: translateX(3.33px);
      -ms-transform: translateX(3.33px);
          transform: translateX(3.33px);
}

@media only screen and (min-width: 800px) {
  .Feed.PostFlow {
    width: 800px;
  }
}



