
.Tooltip.Wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: end;
      align-items: flex-end;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.Tooltip.Headline {
  margin-left: 6vw;
  font-weight: bold;
  font-size: 5vw;
}

.Tooltip.Description {
  margin-left: 10vw;
  font-size: 3vw;
  margin-right: 6vw;
}

@media only screen and (min-width: 800px) {
  .Tooltip.Headline {
    margin-left: 48px;
    font-size: 40px;
  }

  .Tooltip.Description {
    margin-left: 80px;
    font-size: 24px;
    margin-right: 48px;
  }
}



